var EventCenter = {
    on: function(type, handler){
      $(document).on(type, handler)
    },
    fire: function(type, data){
      $(document).trigger(type, data)
    }
  }

class control{
    constructor(){
        this.$root1=$('#app .main ');
        this.$root2=$('#app .footer ');
        this.bind()
    }
    bind(){
        let _this=this;
        this.$root1.find('.hotList .box').on('click','li',function(){
          $('#app').removeClass('active')
          $('#player').addClass('active');
          let channel= $(this).attr('data-channel-id')
          new Player(channel)
        })
    }
    loadMusic(callback){
        _this = this
        clearInterval(_this.clock)
        $.getJSON('https://jirenguapi.applinzi.com/fm/getSong.php?public_tuijian_ktv',{channel: _this.channelId})
        .done(function(ret){
          _this.song = ret.song[0]
          callback()
        })
    }
    setMusic(){
        this.audio.src = this.song.url
        this.$container.find('.detail .author').text(this.song.artist)
        this.$container.find('.detail h1').text(this.song.title)
        this.$container.find('.detail .tag').text(this.channelName)
        $('#page-music .aside .actions .play').removeClass('icon-play').addClass('icon-pause')
        _this.$container.find('.lyric').text('')
        _this.loadLyric()
      }
}
new control()

class Player {
    constructor(xxx) {
      this.$ = selector => document.querySelector('#player')
      this.$$ = selector => document.querySelectorAll('#player')
      this.songList = []
      this.currentIndex = 0
      this.audio = new Audio()
      this.lyricsArr = []
      this.lyricIndex = -1
      this.channelId = xxx;
      this.loadMusic()
    }
    loadMusic() {
      let _this=this;
      $.getJSON('https://jirenguapi.applinzi.com/fm/getSong.php?public_tuijian_ktv',{channel: _this.channelId})
      .done(function(xxx){
        _this.song = xxx.song[0]
        console.log(xxx)
        window.a = xxx.song[0]
        // _this.audio.src = _this.song.url
        // _this.$('.header p').innerText = _this.song.artist;
        // _this.$('.header h1').innerText = _this.song.title;
      })
    }

}